	#keyboardBody{
		position: absolute;
	    height: 100%;
	    width: 100%;
	    z-index: 210000000;
	    background: rgb(45,45,45,0.8);

	}
	.digit{
		width: 100%;
		height: 30%;
		text-align: center;
		color: white;
		font-size: 10em;
	}
	.BoardDiv{
		width: 100%;
		height: 70%;
		position: absolute;
	}
	
	#keyboard {
	    width: 95%;
	    height: 95%;
	    align-items: center;
	    margin-left: 2.5%;
			background:	#d5d9dc  url('') repeat-x;
			-moz-border-radius-topleft: 7px 21px;
			-moz-border-radius-topright: 7px 21px;
			-moz-border-radius-bottomright: 10px;
			-moz-border-radius-bottomleft: 10px;
			border-top-left-radius: 7px 21px;
			border-top-right-radius: 7px 21px;
			border-bottom-right-radius: 10px;
			border-bottom-left-radius: 10px;
			padding: 50px 0 0 10px;
			-webkit-box-shadow:  
				inset 0 0 8px #bbb,
				0 1px 0 #aaa,
				0 4px 0 #bbb,
				0 10px 30px #111;
			-moz-box-shadow:  
				inset 0 0 8px #bbb,
				0 1px 0 #aaa,
				0 4px 0 #bbb,
				0 10px 30px #111;
			box-shadow:  
				inset 0 0 8px #bbb,
				0 1px 0 #aaa,
				0 4px 0 #bbb,
				0 10px 30px #111;
	}

	.cf {list-style-type: none; width: 100%; margin: 0 auto;}
	.cf li {float: left;    width: 33%;}


.key{
	display: block;
	cursor: pointer;
	color: #aaa;
	font: bold 9pt arial;
	text-decoration: none;
	text-align: center;
	width: 80%;
	height: 30%;
	margin: 5px;
	background: #222;
	-moz-border-radius: 4px;
	border-top: 1px solid #222;

	text-shadow: 0px -1px 0px #000;}

.key:active, .keydown {
	color: #888;
	background: #222;
	margin: 7px 5px 3px;
	-webkit-box-shadow:
		inset 0 0 25px #333,
		0 0 3px #333;
	-moz-box-shadow: 
		inset 0 0 25px #333,
		0 0 3px #333;
	box-shadow: 
		inset 0 0 25px #333,
		0 0 3px #333;
	border-top: 1px solid #000;}
	
.key span {
	font-size: 5em;
	margin-top: 5px;

}


#numbers li a span {
	display: block;}
	
#numbers li a b {
	margin: 3px 0 3px;
	display: block;}





