.loading{
	z-index: 999999999;
}

.loading > div {
	position    : absolute;
	top         : 35%;
	left        : 50%;
	text-align  : center;
	font-size   : 16px;
	font-weight : bold;
	color       : #FFF;
	line-height : 3;
	/*height     : 30%;*/
	/*width      : 50%;*/
	/*margin     : -15% 0 0 -25%;*/
	/*background : #999;*/
}

.loader {
	border        : 16px solid #f3f3f3; /* Light grey */
	border-top    : 16px solid #3498db; /* Blue */
	border-radius : 50%;
	width         : 120px;
	height        : 120px;
	animation     : spin 2s linear infinite;
	margin        : auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}