body {
  width       : 100%;
  height      : 100%;
  margin      : 0;
  padding     : 0;
  background  : #f5f6f8;
  font-family : -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  overflow    : hidden;
}

.AsideRights{
  position           : absolute;
  margin-left        : 65%;
  margin-top         : 60px;
  width              : 35%;
  -webkit-transition : width 0.5s;
  transition         : 0.5s;
  height             : calc(100% - 60px);
  background         : #f0f4f4;
  z-index            : 1000;
  right              : -35%;
  /*display            : none;*/
}

.closeDetail{
  background-color : #f8f8f8;
  color            : #333;
  font-size        : 13px;
  font-weight      : 600;
  line-height      : 1;
  padding          : 5px;
  letter-spacing   : 0.6px;
  text-transform   : uppercase;
  cursor           : pointer;
}

.itemCantidad, .deleteItem{
  width       : 10%;
  float       : left;
  font-weight : bold;
  text-align  : justify;
  cursor      : pointer;
  margin-top  : 3px;
}

.deleteItem{
  color: #2a80b9;
}
.nameItem{
  width: 80%;
  float: left;
  font-weight: bold;
  cursor: pointer;

}
.nameItem span{

  font-weight: normal;
  font-size: 13px;
}

.contentDetail{
  height: calc(100% - 36px);
}

.bodyItem{
  height   : 60%;
  overflow : auto;

}
.totales{
  width: 100%;
  margin-top: 5px;
  padding-left: 10%;
  display: flex;
}

.botonResume {
  color: #fff;
    background-color: #2a80b9;
    text-align: center;
    height: 60px;
    font-weight:bold;
    width: 80%;
    margin-left: 10%;
    text-align: center;
    cursor: pointer;
}

.botonResume span{
  margin-left : 30%;
  margin-top  : 5%;
  font-size   : 20px;
}
.totales div{
  width: 45%;
  float: left;
  padding: 10px;
   border-bottom:1px solid silver;
}

.totales :first-child {
  font-weight: bold;
}
.resumeBar{
  height     : calc(40% - 37px );
  text-align : center;
  position   : fixed;
  width      : 35%;
  bottom     : 0px;
}
.resumeBar hr{
  width: 90%;
  border:1px solid silver;    margin-left: 5%;
}
.App{
  width    : 100%;
  height   : 100%;
  position : absolute;
}

.header{
  width            : 100%;
  height           : 60px;
  color            : #fff;
  background-color : #2a80b9;
  float            : left;
}
#root{
      width: 100%;
    height: 100%;
}
.sidebar{
  float      : left;
  width      : 15%;
  height     : calc(100% - 60px);
  background : #fff;
  /*z-index: 1000;*/
  position: relative;
}

.sidebar li,ul{
  margin:0;
  padding:0;
}

.sidebar li{
  text-decoration : none;
  list-style-type : none;
  height          : 50px;
  color           : #3d5170;
  cursor          : pointer;
  text-align      : center;
}
.navigate_next{
  float: right;
  height: 25px;
  width: 50px;
  margin-top: 15px;
  cursor: pointer;
  transform: rotate(180deg);
  transition: 0.8s;
}
.Content{
  width       : calc(100% - 15%);
  float       : left;
  top         : 60px;
  height      : calc(100% - 60px);
  background-color: #2d2d2d;
  color: white;
  position: fixed;
  overflow-x: auto;
  position: initial;
}

.Content .title{
  text-align     : center;
  font-weight    : bold;
  font-size      : 16px;
  width          : 100%;
  padding-top    : 10px;
  padding-bottom : 15px;
}

.slideIcon{
  float: left;
  width: 50px;
  margin-top: 20px;
  display: none;
}
.Content .cajas .label{
  text-align : center;
}

.Logo{
  height: 60px;
  width: 170px;
  float: left;
}

.imageLogo{
  float: left;
}
.TextLogo{
  line-height: 31px;
  font-weight: bold;

}

.contenedorLogo{
   position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}

.UserInfo{
  float: right;
  height: 100%;
  padding: 20px 20px 0px 20px;

}

.SliderbarLi:hover{
  box-shadow: inset 0.1875rem 0 0 #007bff;
    background-color: #fbfbfb;
    color: #007bff;
}

.SliderbarText{
    width: calc(100% - 30px);
    float: left;
    margin-top: 14px;
    text-align: left;
        font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
}

.SliderbarIcon{
      width: 30px;
    float: left;
    margin-top: 15px;
    text-align: center;
}

.itemDetail{
  width: calc(100% - 3px);
  height: 60px;
  padding: 16px 16px 0;
  background-color: white;
  border-bottom: 1px solid #e5eaed;
  /*margin-bottom: 2px;*/
  border-bottom: 1px solid #CCC;
  /*box-shadow: 2px 2px 2px grey;*/

}



@media (max-width: 770px) {
  .sidebar {
        float: left;
    width: 40%;
    height: calc(100% - 60px);
    position: absolute;
    background: #fff;
    margin-top: 60px;
    display: none;
  }

  .slideIcon{
    display: block;
    cursor: pointer;

  }

  .Content{
    width       : 100%;
    float       : none;
   z-index: 6;
  }

  .win-marco{
        width: 90% !important;
    height: 85% !important;
    overflow-x:auto;
  }

  .AsideRights{
    margin-left:35%;
    width: 65%;

  }

}



.table{
  border-collapse : collapse;
  width           : 100%;
  font-family     : Poppins-Regular;
}

.table thead tr td {
  color       : #fff;
  background  : #36304a;
  font-size   : 14px;
  font-weight : unset;
  padding     : 10px;
}

.table tbody tr td{
  color         : #666;
  font-size     : 12px;
  padding       : 10px;
  border-bottom : 1px solid #f2f2f2;
}