.mesa{
  width: 120px;
  height: 120px;
  float:left;
  background-color: white;
  color: black;
  margin:10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 40px;
  border-radius: 60px;
  cursor: pointer;
  color: #FFF;
  /*box-shadow: 0 0.125rem 9.375rem rgba(90,97,105,.1), 0 0.25rem 0.5rem rgba(90,97,105,.12), 0 0.9375rem 1.375rem rgba(90,97,105,.1), 0 0.4375rem 2.1875rem rgba(165,182,201,.1);*/
}


.contentMesa{
	z-index: 10;
  position: absolute;
  width: calc(100% - 150px);
}

.contentEstadosMesa{
  padding-top: 5px;
  padding-bottom: 10px;
  width      : 100%;
  text-align : -webkit-center;
  /*text-align: center;*/
}

.contentEstadosMesa table thead tr td{
  font-weight: bold;
  font-size: 16px;
  text-align: center;
}

.contentEstadosMesa table tbody tr td{
  padding: 3px;
}

.contentEstadosMesa table tbody tr td:nth-child(3n){
  width: 20px;
}

.barraCuenta{
	float: right;
    width: 40%;
    background: white;
    z-index: 10000;
    height: 100%;
    color: black;
    position: relative;
}

.Comensales{
width: 80%;
    height: 80%;
    margin-left: 10%;
    margin-top: 5%;
    /* background: white; */
    background-color: #FFFFFF;
}

.numberPickers{
  width: 80px;
  height: 80px;
  background-color: #2d2d2d;
  margin: 5px;
  padding: 5px;
  /*border-radius: 9px;*/
  /*box-shadow: 0 0.125rem 9.375rem rgba(90,97,105,.1), 0 0.25rem 0.5rem rgba(90,97,105,.12), 0 0.9375rem 1.375rem rgba(90,97,105,.1), 0 0.4375rem 2.1875rem rgba(165,182,201,.1);*/
  margin: auto;
  margin-top: 10px;
}

.columC{
  width: 25%;
  height: 80%;
  float: left;
  text-align: center;
  font-weight: bold;
  color: black;
     justify-content: center;
     display: flex;
   align-items: center;
}

.ComensalSihos{
  width: 100%;
  height: 100%;
}

.headerH,.filaTable{
  width: 100%;
  padding: 10px;
      border-bottom: 1px solid #dbdbdb;
}

.filaTable{
      height: 50px;
}

.columnTable div{
  width: 60% !important;
  color: white;
  height: 30px;
    align-items: center;
  justify-content: center;
  display: flex;
}
.columnTable{
  width: 33%;
  float: left;
  height: auto;
  align-items: center;
  justify-content: center;
  display: flex;
  text-align: center;
}
.iconC{
  width: 50px;
  height: 50px;
  background-color: #2d2d2d;
  border-radius: 9px;
  margin: auto;
}
.btnNext{
background-color: #2a80b9;
    width: 30%;
    display: flow-root;
    height: 50px;
    margin-left: 35%;
    text-align: center;
    cursor: pointer;
}
.btnNext span{
  font-size: 20px;
}
.numberContent{
  width: 100%;
  height: 40%;
  color: white;
  font-weight: bold;
  font-size: 35px;
     display: flex;
   justify-content: center;
   align-items: center;
}
.numberContentFlecha{
  width: 100%;
  height: 30%;
  text-align: center;
}

.arrowAumentar{
      border-top: 3px solid #ddd;
    border-left: 3px solid #ddd;
    height: 20px;
    width: 20px;
    display: inline-block;
    cursor: pointer;
    margin: auto;
    margin-top:5px;
    transform: rotate(-315deg);
}

.arrowDisminuir{
      border-top: 3px solid #ddd;
    border-left: 3px solid #ddd;
    height: 18px;
    width: 18px;
    display: inline-block;
    cursor: pointer;
    margin: auto;
    margin-bottom: 5px;
    transform: rotate(-135deg);
}