.win-modal {
	visibility : hidden;
	opacity    : 0;
}

.win-modal:target{
	visibility       : visible;
	opacity          : 1;
	background-color : rgba(0,0,0,0.29);
	position         : fixed;
	top              : 0;
	left             : 0;
	right            : 0;
	bottom           : 0;
	margin           : 0;
	width            : 100%;
	height           : 100%;
	z-index          : 999;
	transition       : all 0.5s;
}

.win-title-btn a{
	position         : absolute;
	top              : 3px;
	right            : 3px;
	/*background-color : #333;*/
	padding          : 1px 7px;
	/* font-size     : 20px; */
	text-decoration  : none;
	/* line-height   : 1; */
	color            : #fff;
}

