    background-size: 100% 100%;
	/*background-image: url(https://www.comedera.com/wp-content/uploads/2013/07/arroz-con-pollo.jpg);*/
}

.card {
    float                       : left;
    cursor                      : pointer;
    width                       : 150px;
    display                     : flex;
    flex-direction              : column;
    position                    : relative;
    margin                      : 0 0 2rem;
    background-color            : #fff;
    border-radius               : 2px;
    font-size                   : 1rem;
    font-weight                 : 400;
    color                       : rgba(0, 0, 0, 0.87);
    box-shadow                  : 0 1px 3px 0 rgba(0, 0, 0, 0.21);
    /*  overflow                : hidden;*/
    z-index                     : 1;
    -webkit-tap-highlight-color : transparent;
    -webkit-tap-highlight-color : rgba(255, 255, 255, 0);
    margin                      :10px;
}

.card .card-img-cover {
    position : relative;
    color    : #fff;
}

.card .card-content {
    background-color           : rgba(0, 0, 0, 0.7);
    position                   : absolute;
    right                      : 0;
    bottom                     : 0;
    left                       : 0;
    z-index                    : 2;
    border-bottom-left-radius  : 2px;
    border-bottom-right-radius : 2px;
}

.card .card-content .card-header {
    padding         : 1rem;
    display         : flex;
    flex-direction  : column;
    justify-content : center;
    color           : #fff;
}
.card .card-content .card-header .card-title {
    margin         : 0;
    font-size      : 0.9rem;
    font-weight    : 400;
    letter-spacing : 0;
    line-height    : 15px;
}

