.contentMenuBtns{
	height: 100%;
	float  : right;
}

.nav{
	width: 60px;
	float  : left;
	height : 100%;
	text-align: center;
}

.nav:hover  .nav ul {
	max-height: 1000%;
}

.nav ul{
	display    : block;
	/*position   : absolute;*/
	position   : relative;
	z-index    : 999;
	/*margin     : 0em 0em 0;*/
	margin     : 10px 0em 0 0;
	background : #2a80b9;
	padding    : 0;
	/*transition: 0.5s;*/
	/*height: 0px;*/
	overflow: hidden;
	max-height: 0;
    overflow: hidden;
    -webkit-transition: max-height1s;
    -moz-transition: max-height 1s;
    transition: max-height 1s;
	/*margin-left: -4%;*/
	/*left       : 10%;*/
}

.nav ul li{
    list-style: none;
    border-top: 1px solid #459bd4;
    text-align: center;
}

.nav ul li a{
	display: block;
    color: #fff;
    text-decoration: none;
    font-size: 0.9em;
    /*padding: 1.14em 1.4em;*/
    padding: 1.14em 0.8em;
    vertical-align: sub;
    font-size: 11px;
}

.nav ul li a i{
    display: inline-block;
    width: 25px;
    height: 25px;
    vertical-align: bottom;
    margin: 0 0 0.5em 0;
	width     : 100%;
}

.menuBtn{
	height : 100%;
	cursor : pointer;
	/*margin : 0px 3px 0px 3px;*/
	text-align: center;
}

/*.nav ul li:hover .nav{
	background-color: #459bd4;
}*/

.nav:hover,.nav ul li:hover {
	background-color: #459bd4;
}

.menuBtn .material-icons{
	font-size : 40px;
	padding   : 5px;
}