.contentAside{
	width    : 100%;
	height   : calc(100% - 60px);
	position : fixed;
	bottom: 0px;
	/*z-index  : 10;*/
}

#comensales{
	display: none;
}
.descuentosD{
	padding: 5px;
}
.descuentosD select,input{
	width: 90%;
	margin-left: 5%;
	height: 30px;
}

.propinaInput select,input {
	width: 44%;

}

.numberItemcant{
	width: 40px;
}
.fontSize12{
	font-size: 12px !important;
	font-weight: bold;
}
.titleResumen , .priceResumen{
	font-size: 14px !important;
	height: 25px !important;
}

.contentBtns{
	padding-left : 5px;
	color        : #FFF;
	font-weight  : bold;
}

.contentBtns > div {
	background-color : #2a80b9;
	padding          : 0px;
	margin           : 5px;
	cursor           : pointer;
}

.contentBtns > div:hover{
	background-color : #2a80b9b5;
}

.contentBtns i{
	width: 100%;
}

.BodyPay{
	    height: 95%;
    width: 95%;
    margin-left: 2.5%;
    margin-top: 1.5%;
    background-color: #FFFFFF;
}


.contenedorMetodosPago{
	margin-top: 5px;
}
.columItem {
	width: 45%;
	float: left;
	color: black;
	margin-top:5px;
	margin-left:2.5%;
	padding: 0;
	height: 100%;
}

.contentDetailsComensales, .contenedorMetodosPago{
	width: 100%;
	float: left;
	height: 35px;
}

.contentDetailsComensales div:nth-child(1n) {
	width: calc(100% - 30px - 30px) !important;
	float: left;
}
.contentDetailsComensales div:nth-child(2n) {
	width: 30px !important;
	float: left;
}
.contentDetailsComensales div:nth-child(3n) {
	width  : 30px !important;
	float  : left;
	cursor : pointer;
}


.contenedorMetodosPago div:nth-child(1n), .contenedorMetodosPago div:nth-child(2n) {
	width: calc(50% - 15px) !important;
	float: left;
}
/*.contenedorMetodosPago div:nth-child(2n) {
	width: 30px !important;
	float: left;
}*/
.contenedorMetodosPago div:nth-child(3n) {
	width  : 30px !important;
	float  : left;
	cursor : pointer;
}

.detailtitleItem,.detailistItem{
	width: 50%;
	float: left;
	border-bottom: 1px solid #dbdbdb;
vertical-align: text-bottom;

	height: 40px;
}

.detailtitleItem{
	font-weight: bold;
	border-width: 0 0 2px;
}

.titleItem,.listItem{
	width: 100%;
	margin-top: 10px;
}

.detailtitleItem{font-size: 18px;line-height:2;}

.inputMetodos{
	width: 80%;
}

.backgroundSombra{
	    background-color: #fafafa;

}
.center{ text-align: center; }


.contenidoItem{
	width: 100%;
	height: 70%;
	overflow-x: auto;
}
.resumeItem{
	width: 100%;
	height: 30%;
}

.optionItem{
	cursor: pointer;
	font-weight: 100;
}

.selectOptionItem{
	color: #2a80b9;
	border-bottom: 1px solid #2a80b9;
}

.noBorder{
	border-bottom: 0;
}

.generarPago{
	width: 80%;
	font-size: 18px;
	font-weight: bold;
	margin-left: 10% !important;
	height: 50px;
	text-align: center;
	 display: flex;
   justify-content: center;
   align-items: center;
}
.acordeon{
	margin-top: 55px;
}
.acordeon__titulo {
	display: block;
	padding: 10px;
	background: #2a80b9;
	color: #fff;
	font-size: 15px;
	cursor: pointer;
	border-bottom: 1px solid #fff;
}
.acordeon__titulo:hover {
	background: rgba(42,128,185, 0.8);
}

.acordeon__contenido {
	height: 0;
	overflow: hidden;
	margin: 0;
	transition: all 0.5s;
}

.acordeon input:checked ~ .acordeon__contenido{
	height: auto;
	margin: 15px 0;
}

.acordeonInput{
	display: none;
}

small{
	color: #9f9898;
	font-size: 1rem;
	padding: 10px;
}
